import React from 'react';
import ReactDOM from 'react-dom';

class Head extends React.Component {
  render () {
    return (
      <div class="nes-container with-title">
        <div>My Page</div>
      </div>
    );
  }
}

class Who extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      name: "rita",
      mail: "wkryz1204@gmail.com",
      who: false,
    };
  };

  handleClick () {
    this.setState({
      who: !this.state.who,
    });
  };

  render () {
    return (
      <div class="nes-container with-title" onClick={() => this.handleClick()}>
        {this.state.who ? <div>Who:clicked</div> : <div>Who:click</div>}
        <ul class="nes-list is-disc">
          {this.state.who ? <li>Name: {this.state.name}</li> : <div>&nbsp;</div>}
          {this.state.who ? <li>Mail: {this.state.mail}</li> : <div>&nbsp;</div>}
        </ul>
      </div>
    );
  }
}

class Contents extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      contents: false,
    };
  };

  handleClick () {
    this.setState({
      contents: !this.state.contents,
    });
  };

  render () {
    return (
      <div class="nes-container with-title "onClick={() => this.handleClick()}>
        {this.state.contents ? <div>Contents:clicked</div>: <div>Contents:click</div>}
        <ul class="nes-list is-disc">
          {this.state.contents ? <li><a href="https://github.com/ritarock">github</a></li> : <div>&nbsp;</div>}
          {this.state.contents ? <li><a href="http://ritarock.hatenablog.com/">hatena blog</a></li> : <div>&nbsp;</div>}
          {this.state.contents ? <li><a href="https://qiita.com/ritarock">qiita</a></li> : <div>&nbsp;</div>}
        </ul>
      </div>
    );
  }
}

class Main extends React.Component {
  render () {
    return (
      <div>
        <Head />
        <Who />
        <Contents />
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Main />,
  document.getElementById('root')
);

